 {\n  padding-right: 20px;\n}\n\n.leave-timeline {\n  position: relative;\n  max-width: 1200px;\n  margin: 0 auto;\n}\n\n.leave-list {\n  margin-left: 50px;\n}\n\n.leave-list .leave-type {\n  border-radius: 50px;\n  border: solid 1px #FE9C45;\n  color: #FE9C45;\n  padding: 2px 10px;\n  font-size: 14px !important;\n  white-space: nowrap;\n}\n\n.leave-list .custom-icon-calendar-icon {\n  color: #FE9C45;\n}\n\n.leave-list .end {\n  text-align: end;\n  color: #FE9C45;\n  white-space: nowrap;\n  margin: auto;\n}\n\n.leave-list .date {\n  color: black;\n}\n\n.leave-list .action {\n  font-size: 12px;\n}\n\n/* The actual timeline (the vertical ruler) */\n\n.leave-timeline::after {\n  content: \"\";\n  position: absolute;\n  width: 2px;\n  background-color: #D2D2D2;\n  top: 0;\n  bottom: 0;\n  left: 50%;\n}\n\n/* Container around content */\n\n.leave-container {\n  position: relative;\n  background-color: inherit;\n}\n\n/* The circles on the timeline */\n\n.leave-container::after {\n  content: \"\";\n  position: absolute;\n  width: 15px;\n  height: 15px;\n  top: 15px;\n  border-radius: 50%;\n  z-index: 1;\n  margin-left: 9px;\n}\n\n.leave-container:nth-of-type(odd)::after {\n  background-color: #0A15D5;\n}\n\n.leave-container:nth-of-type(even)::after {\n  background-color: #FF9F55;\n}\n\n/* Place the container to the left */\n\n.left {\n  left: 0;\n}\n\n/* Place the timelime to the left */\n\n.leave-timeline::after {\n  left: 31px;\n}\n\n/* Make sure all circles are at the same spot */\n\n.left::after,\n.right::after {\n  left: 15px;\n}\n\n.fab-add-btn {\n  --border-radius: 15px !important;\n  --background: #FE9C45;\n}\n\nion-fab {\n  position: fixed !important;\n  z-index: 999;\n}\n/*# sourceMappingURL=data:application/json;base64, */"